ks-filter {
    display: block;
    margin-bottom: 10px;
    user-select: none;
}

ks-filter details {
    border: none;
    padding: 0;
}

ks-filter summary {
    font-family: var(--font-emphasis);
    font-size: 1rem;
    font-weight: 700;
    list-style: none;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

ks-filter summary::-webkit-details-marker {
    display: none;
}

ks-filter summary::-moz-list-bullet {
    list-style-type: none;
}

ks-filter summary > ks-icon {
    flex-shrink: 0;
}

ks-filter .filter-content {
    font-size: 0.9rem;
    font-weight: 500;
    padding: 10px 0;
    animation: fade-in 0.3s ease 1;
}

ks-filter details[open] .filter-content {
    animation: fade-in 0.3s ease 1;
}