import { Component, h, Host, Prop, Listen } from '@stencil/core';

@Component({
    tag: 'ks-filter',
    styleUrl: 'filter.css'
})
export class Filter {

    @Prop() name: string;
    @Prop({mutable: true, reflect: true}) active: boolean = false;

    @Listen('toggle')
    onToggle(event: Event) {
        const detailsElement = event.target as HTMLDetailsElement;
        this.active = detailsElement.open;
    }

    render() {
        return <Host>
            <details open={this.active}>
                <summary>
                    {this.name}
                    <ks-icon name={this.active ? "minus" : "plus"} size={0.9} aria-label={this.active ? "Zwiń" : "Rozwiń"}/>
                </summary>
                <div class="filter-content">
                    <slot/>
                </div>
            </details>
        </Host>;
    }
}
